//     %   
export default function calculatePercentage(percentage: string) {
    switch (percentage) {
        case '10%':
            return 0.1;
        case '25%':
            return 0.25;
        case '50%':
            return 0.5;
        case '75%':
            return 0.75;
        case '100%':
            return 1;
        default:
            return 0; //       
    }
}