//      input  
export default function adjustInputValue(value: string) {
    if (!value) {
        return '0';
    }

    //  "."   
    value = value.replace(/[^0-9.]/g, "");

    //      
    const parts = value.split('.');
    if (parts.length > 1) {
        value = parts[0] + '.' + parts.slice(1).join('');
    }

    // 00, 01, 02, ...    0    ,   0  
    if (value[0] === '0' && value.length > 1) {
        //    
        if (value[1] !== '.') {
            value = value.replace(/^0+/, '');
        }
    }

    // "."     
    if (value[0] === '.') {
        value = '0' + value;
    }

    // value   '0' (NaN )
    if (value === '') {
        value = '0';
    }

    return value;
}