//        
export default function convertToDate(timestamp: number) {
    const date = new Date(timestamp);

    let tradeTime = new Intl.DateTimeFormat('ko-KR', {
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false, // 24 
    }).format(date);
    tradeTime = tradeTime.replace(". ", "/").replace(".", "").replace(" ", "").replace(" ", "")

    return tradeTime;
}