type Props = {
    label: string;
    isWarning: boolean;
    isSubmitted?: boolean;
}

export default function TransferWarning({ isWarning, label, isSubmitted }: Props) {
    //     , isWarning true  
    if (isSubmitted === undefined) {
        if (!isWarning) {
            return null;
        }
        return <div className="warning-KRW">{label}</div>;
    }
    else {
        //    , isWarning isSubmitted  true  
        if (!isWarning || !isSubmitted) {
            return null;
        }
        return <div className="warning-KRW">{label}</div>;
    }
}