import axios from "axios";
import { useAppDispatch, useAppSelector } from "../../redux/hooks";
import { setUserBalance } from "../../redux/features/user/userSlice";
import { showNoticeModal } from "../../redux/features/ui/modalSlice";
import { useEffect } from "react";

const API_URL = process.env.REACT_APP_API_URL;

export default function useGetBalance() {
    const dispatch = useAppDispatch();

    const user = useAppSelector(state => state.user);

    //    
    const getBalance = async () => {
        try {
            const response = await axios.get(
                `${API_URL}/api/user/balance/`,
                { withCredentials: true }  //    
            );
            dispatch(setUserBalance(response.data.balance));
        } catch (error) {
            dispatch(showNoticeModal({
                content: '   .    .',
            }));
        }
    }

    useEffect(() => {
        if (user.email) {
            getBalance();
        }
    }, [user.email]);

    return getBalance;
}